import 'bulma/css/bulma.css';
import Head from 'next/head';
import Navbar from './Navbar';

const Layout = (props) => (
	<div>
		<Head>
			<title>J-Sleeve</title>
			<meta name="viewport" content="width=device-width, initial-scale=1" />
		</Head>
		<Navbar />
		{props.children}
	</div>
);

export default Layout;